<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定热力图加载的 Cesium 对象类型，0: RectangleGeometry, 1: RectangleGraphics, 2: SingleTileImageryProvider。</td>
</tr>
<tr>
<td>bounds</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定热力图矩形范围。</td>
</tr>
<tr>
<td>options</td>
<td>Object</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定热力图的 heatmap 参数。</td>
</tr>
<tr>
<td>min</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定最小值。</td>
</tr>
<tr>
<td>max</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定最大值。</td>
</tr>
<tr>
<td>data</td>
<td>Array</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定热力图数据。</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定热力图是否显示。</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及 cesiumObject（根据 type 不同分别返回 0: GroundPrimitive, 1: Entity, 2: ImageryLayer）</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
      data() {
        return {
          show: true,
          bounds: { west: 80.0, south: 30.0, east: 109.0, north: 50.0 },
          options: {
            backgroundColor: 'rgba(0,0,0,0)',
            gradient: {
              // enter n keys between 0 and 1 here
              // for gradient color customization
              '0.9': 'red',
              '0.8': 'orange',
              '0.7': 'yellow',
              '0.5': 'blue',
              '0.3': 'green'
            },
            // minCanvasSize: 10,
            // maxCanvasSize: 100,
            radius: 250,
            maxOpacity: 0.5,
            minOpacity: 0,
            blur: 0.75
          },
          data: [],
          min: 0,
          max: 0
        }
      },
      methods: {
        ready(cesiumInstance) {
          window.vm = this
          this.cesiumInstance = cesiumInstance
          const { Cesium, viewer } = this.cesiumInstance
          let _this = this
          Cesium.Resource.fetchJson({ url: './statics/SampleData/heatmapData/19042808_t.json' }).then((data) => {
            _this.bounds = {
              west: data.left,
              south: data.bottom,
              east: data.right,
              north: data.top
            }
            _this.min = data.min
            _this.max = data.max
            _this.data = data.datas
          })
        },
        subReady({ Cesium, viewer, cesiumObject }) {
          this.$refs.heatmap.$refs.childRef.createPromise.then(({ Cesium, viewer, cesiumObject }) => {
            console.log(cesiumObject)
            if (cesiumObject instanceof Cesium.GroundPrimitive) {
              setTimeout(() => {
                const geometry = cesiumObject.geometryInstances.geometry.constructor.createGeometry(cesiumObject.geometryInstances.geometry)
                viewer.scene.camera.flyToBoundingSphere(geometry.boundingSphere)
              }, 500)
            } else if (cesiumObject instanceof Cesium.Entity) {
              viewer.flyTo(cesiumObject)
            } else {
              viewer.camera.flyTo({ destination: cesiumObject.imageryProvider.rectangle })
            }
          })
        },
        getData(data) {
          var result = []
          let rows = data.rows
          let cols = data.cols
          let cellX = (data.right - data.left) / cols
          let cellY = (data.top - data.bottom) / rows
          for (let i = 0; i < rows; i++) {
            for (let j = 0; j < cols; j++) {
              let x = data.left + i * cellX
              let y = data.bottom + j * cellY
              let value = data.dvalues[i * cols + j]
              if (value !== data.noDataValue) {
                result.push({ x: x, y: y, value: value })
              }
            }
          }
          return result
        }
      }
    }
  </script>